p{
    color: greenyellow;
}
.footer_section{
    height: 291px;
    background-color: #55E5A0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 160px;
    max-width: 1920px;
}

.logo_footer{
    height: 100px;
    border-radius: 200px;

}

.name_impressum_footer{
    
   p{color: black;
     font-size: 19px;
     font-family: 'Montserrat';
     font-weight: bold;
     
}
a{
    text-decoration: none;
    color: rgb(46, 46, 46);
    font-size: 25px;
    font-weight: bold;
   
    background: linear-gradient(to bottom, var(--mainColor) 0%, var(--mainColor) 100%);
	background-position: 0 100%;
	background-repeat: repeat-x;
	background-size: 3px 3px;
    font-family: 'Montserrat';
  text-decoration: none;
  cursor: pointer;
  &:hover {
    color: #2c7753;

    background-position: 0 100%;
    background-size: auto 6px;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg id='squiggle-link' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:ev='http://www.w3.org/2001/xml-events' viewBox='0 0 20 4'%3E%3Cstyle type='text/css'%3E.squiggle{animation:shift .3s linear infinite;}@keyframes shift {from {transform:translateX(0);}to {transform:translateX(-20px);}}%3C/style%3E%3Cpath fill='%232c7753' stroke='%232c7753' stroke-width='2' class='squiggle' d='M0,3.5 c 5,0,5,-3,10,-3 s 5,3,10,3 c 5,0,5,-3,10,-3 s 5,3,10,3'/%3E%3C/svg%3E");
    text-decoration: none;
 
  }
}

text-align: center;
}

.invert{

    img{margin-left: 42px;}
    transition: transform 0.7s;
    -webkit-filter: invert(1);
    filter: invert(1);
    &:hover{ transform: scale(1.5, 1.5);
  
    }
    margin-left: 36px;
    cursor: pointer;

}
@media (max-width:635px) {
.invert{
    height: 30px;
    margin-left: 1px;
    margin-right: 10px;
}
.logo_footer{
    height: 50px;
    margin-left: 0px;
}
.name_impressum_footer{
    a{
font-size: 20px;

    }
p{
    font-size: 12px;
 
}
}
.footer_section{
    flex-direction: column-reverse;
    align-items: center;
 margin-top: 60px;
 height: 200px;
}

}

